import type { Metadata } from "next";
import { Space_Grotesk, JetBrains_Mono } from "next/font/google";
import "./globals.css";

// const spaceGrotesk = Space_Grotesk({
// 	subsets: ["latin"],
// 	variable: "--font-space-grotesk",
// 	display: "swap",
// });

const jetbrainsMono = JetBrains_Mono({
	subsets: ["latin"],
	variable: "--font-jetbrains-mono",
	display: "swap",
});

export const metadata: Metadata = {
	title: "VetAI",
	description: "AI Diagnostics",
};

export default function RootLayout({
	                                   children,
                                   }: Readonly<{
	children: React.ReactNode;
}>) {
	return (
		<html lang="en" suppressHydrationWarning>
		<head>
			<script
				dangerouslySetInnerHTML={{
					__html: `
            try {
              if (localStorage.theme === 'dark' || (!('theme' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches)) {
                document.documentElement.classList.add('dark')
              } else {
                document.documentElement.classList.remove('dark')
              }
            } catch (_) {}
          `,
				}}
			/>
		</head>
		<body
			// FIX: Added 'font-sans' here.
			// Also added 'bg-background' to ensure the dark theme color applies immediately.
			className={`${jetbrainsMono.variable} font-sans antialiased bg-background text-foreground`}
		>
		{children}
		</body>
		</html>
	);
}